.App {
  text-align: center;
}

a {
  color: #0080ff;
}

a:hover {
  color: #004bee;
  /* background-color: #f9d9a115; */
}

a.logo {
  font-family: "futura", serif;
  text-decoration: none;
  font-size: 36px;
  color: white
}

a.logo:hover {
  font-family: "futura", serif;
  text-decoration: underline;
  font-size: 36px;
  color: white
}

.face-image {
  width: 12.5vw;
  height: 12.5vw;
  border-radius: 1.04vw;
  background-color: #dedede;
  z-index: 5;
  box-shadow: 1vw 1vw 2.5vw #1a1a1a;
  padding-left: 0;
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  /* flex-direction: column; */
  align-items: left;
  justify-content: Top;
  font-size: calc(10px + 2vmin);
  text-align: left;
  color: white;
  line-height: 0.3;

  font-family: pt-mono, monospace;
  font-style: normal;
  font-weight: 400;
}

#bgVideo {
  /* Set rules to fill background */
  min-height: 1080px;
  min-width: 1920px;

  /* Set up proportionate scaling */
  width: auto;
  height: auto;

  /* Set up positioning */
  position: fixed;
  top: 50%;
  margin-top: -475px;
  left: 0;

  left: 50%;
  margin-left: -960px;
  /* 50% */
}

.no-drag-image {
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.App-body {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  /* align-items: left; */
  justify-content: Top;
  font-size: calc(10px + 2vmin);
  text-align: left;
  color: white;
  line-height: 0.3;
  padding-top: 20;
  border: 1px solid #c3c3c3;

  font-family: pt-mono, monospace;
  font-style: normal;
  font-weight: 400;
  overflow-x: hidden;
}

.flex-row-container {
  display: flex;
  flex-direction: row;
  justify-content: left;
  /* border: 1px solid green; */
}

.flex-column-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* padding-left: 25vmin; */
  /* border: 10px solid green; */
}

/* .comment-text {
  padding-left: 25vmin;
  font-size: 1.2vw;
} */

.code-font {
  font-family: pt-mono, monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 1.6vw;
}

.center-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* border: 10px solid blue; */
  /* text-align: center; */
  min-height: 100vh;
  min-width: 100vw;
}

.App-link {
  color: #61dafb;
}

.overlayedPlaceholder {
  z-index: 1;
  position: absolute;
  color: #a3a3a3;
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

pre {
  color: white;
  font-family: monospace;
  padding: 1ch;
}

pre.caret-block>span {
  background-color: hsla(1, 1%, 100%, 1);
  color: #000;
}

@keyframes blink {

  from,
  to {
    border-color: transparent;
  }

  50% {
    border-color: #fff;
  }
}

.rainbowWrapper {
  width: 100vw;
  height: 118vh;
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
}

.logoWrapper {
  position: absolute;
  padding-left: 2vw;
  padding-top: 3vh;
}

.textedit-wrapper {
  background-color: #ffffff;
  border-radius: 0.8vw;
  padding-bottom: 3vw;
  width: 33vw;
  height: 18vw;
  box-shadow: 1vw 1vw 2.5vw #1a1a1a;
  z-index: 1;
  font-size: 1.2vw;
  line-height: 0.5;
}

.top-console-bar {
  background-color: #ffffff;
  min-height: 1.8vw;
  border-top-left-radius: 0.8vw;
  border-top-right-radius: 0.8vw;
  margin: auto;
}

.top-console-bar span {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 0.55vw;
  height: 0.85vw;
  width: 0.85vw;
  border-radius: 50%;
  display: inline-block;
}

.top-console-bar span:nth-child(1) {
  background-color: #ff605c;
}

.top-console-bar span:nth-child(2) {
  background-color: #ffbd44;
}

.top-console-bar span:nth-child(3) {
  background-color: #00ca4e;
}

.top-console-bar div:last-child {
  margin-left: 0.3vw;
}

.noDragImage {
  /* height: 166.2px; */
  width: 12.5vw;
  /* aspect-ratio: 1815/1679; */
  border-radius: 1.04vw;
  background-color: #dedede;
  /* padding-top: 10px; */
  z-index: 5;
  box-shadow: 1vw 1vw 2.5vw #1a1a1a;
}

.noDragImage img {
  pointer-events: none;
}

.console-wrapper {
  z-index: 4;
  color: white;
}